.Contacto{
    height: 100%;
    display:grid;
    grid-template-columns:1fr 1fr 1fr;
    grid-template-rows: 1fr 1fr 1fr;
    grid-gap: 5px;
    justify-content: space-evenly;
    align-content:center;
}
.Contacto div{
    /*border: solid red 1px;*/
    box-shadow: #000 5px 5px 5px;
    display: grid;
    justify-content:center;
    align-items: center;   
}
.Contacto img{
    width: 11vw;
    justify-self: center;
}
.Negro img, .Negro2 img{
    grid-column: 2/3;
}
.Negro{
    background-color: #333;
    color: #fff;
    grid-template: 1fr/repeat(4,1fr);
    grid-gap: 20px;
    grid-column: 1/span 2;
    font-size:3vw
}
.Negro2{
    background-color: #333;
    color: #fff;
    grid-template: 1fr/repeat(4,1fr);
    grid-gap: 20px;
    font-size:3vw;
    grid-column:-1/-3;
    grid-row:-1/-2;
}
.Blanco{
    background-color: #fff;
    grid-column: -1/-2;
    grid-row:1/span 2 ;
}
.Blanco2{
    background-color: #fff;
    grid-column: 1/2;
    grid-row:-1/2;
}
.Blanco, .Blanco2{
    grid-template:1fr 1fr 1fr / 1fr;
}
.telefono{
    font-size: 2.2vw;
}
.vertical{
   grid-row: 2/3;
   box-shadow: none !important;
   font-size: 4vw;
}
.contactame{
    grid-column: 2/3;
    grid-row:2/3;
    background-color: #fff;
    animation-name: cambioColor;
    animation-duration: 10s;
    animation-iteration-count: infinite;
    backface-visibility: hidden;
}
.contactame h3, .invisible h3{
    font-size: 3vw;
}
.invisible{
    background-color: #333;
    color:#fff;
    backface-visibility: hidden;
    grid-column: 2/3;
    grid-row:2/3;
    transform: rotateX(-180deg);
    animation-name: reverso;
    animation-duration: 10s;
    animation-iteration-count: infinite;
}

@keyframes reverso{
    50%{
        transform: rotateX(0deg);
    }
}

@keyframes cambioColor{

    50%{
        transform: rotateX(180deg);
    }  

}

