.Trabajos{
    display:grid;
    grid-template: 1fr /1fr;
    width:99%;
    height: 100%;
    overflow: hidden;
    overflow-y: scroll;
}
.Trabajos img{
    width: 100%;
    height: 100%;
}

.misTrabajos, .masTrabajo{
    width: 99%;
    grid-column: 1/2;
    grid-row: 1/2;
    backface-visibility: hidden;
    transition: transform 1s;
}
.misTrabajos{
    display:grid;
    grid-template-columns: repeat(3,1fr);
    grid-template-rows: 13vh;
    grid-auto-rows: 200px;
    grid-gap: 2vh;
}

.Trabajo{
    background-color: #fff;
    display: grid;
    grid-template: "Inicio" 50px "Imagen" 3fr"Fin"/ 1fr;
    text-align: center;
    border-radius: 5px;
    overflow: hidden;
    box-shadow: #000 5px 5px 5px;
}

.masTrabajo{
    display:grid;
    grid-template: auto 1fr auto auto 10px/1fr 1fr 1fr;
    grid-gap: 15px;
    justify-items: center;
}
.masTrabajo h3{
    grid-column: 1/-1;
    grid-row: 1/2;
    color: #fff;
    text-shadow: #000 3px 3px;
    font-size: 5vh;
    margin: 0px;
}
.masTrabajo img{
    grid-column: 1/-1;
    border-radius: 10px;
    box-shadow: #000 5px 5px 5px;
    width: 100%;
    height: auto;
}
.masTrabajo p{
    grid-column: 1/-1;
    border: solid #fff 7px;
    border-radius: 10px;
    font-size: 3vh;
    width:90%;
    padding: 10px;
    color:#fff;
}
.Atras{
    grid-column: 1/2;
    grid-row: 1/2;
    width: 7vh;
    height: 7vh;
    justify-self: left;
}
.Atras img{
    box-shadow: none;
}
.Enlace{
    border: solid #fff 5px;
    background-color: #fff;
    border-radius: 5px;
    grid-column: 2/3;
    font-size: 3vh;
    width: 100%;
    height: 100%;
    color:#000;
    text-decoration: none;
    display:grid;
    justify-items: center;
    align-items: center;
    box-shadow: #000 5px 5px 5px;
}

.Rotar{
    transform: rotateY(180deg)
}

@media (max-width:700px){
    .misTrabajos{
        grid-template-columns:1fr ;
    }
    .masTrabajo{
        grid-template-rows: repeat(7, auto);
    }
    .masTrabajo img{
        width: 100%;
        height: auto;
    }
    .masTrabajo h3{
        grid-row: 2/3;
    }
    .Enlace{
        grid-column: 2;
    }
}