.Visor{
    background-color: #fff2;
    overflow:hidden;
    padding: 15px;
    grid-column: 4/5;
    grid-row: 2/5;
    transition: transform 1s;  
}

.sombra{
    box-shadow: #000 3px 3px 5px 1px;
}


.Arriba{
    animation: arriba 1s ;
}
@keyframes arriba{
    50%{
        transform:  rotateZ(90deg)
        translateY(-200%)
        translateX(-70%)
    }
    51%{
        transform:  rotateZ(-90deg)
        translateY(200%)
        translateX(-70%)
    }
}
.Abajo{
    animation: abajo 1s ;
}
@keyframes abajo{
    50%{
        transform:  rotateZ(-90deg)
        translateY(200%)
        translateX(-70%)
    }
    51%{
        transform:  rotateZ(90deg)
        translateY(-200%)
        translateX(-70%)
    }
}

.Izquierda{
    animation: Izquierda 1s;
}
@keyframes Izquierda{
    50%{
        transform: translateX(-500%)
    }
    51%{
        transform: translateX(500%);
    }
}
.Derecha{
    animation: Derecha 1s;
}
@keyframes Derecha{
    50%{
        transform: translateX(500%)
    }
    51%{
        transform: translateX(-500%);
    }
}

