.Certificados{
    display:grid;
    grid-template-columns:1fr 1fr;
    grid-template-rows:50px;
    grid-auto-rows: 300px;
    grid-gap: 30px;
    overflow-y: scroll;
    padding:0px 10px;
    
    height: 100%;
}
.sombra{
    box-shadow: #000 5px 5px 5px;
}
.titulo{
    justify-self: center;
    grid-column: 1/-1;
}
