@font-face{
  font-family:'Robota';
  src: url('./Fuente/Robota.otf');
}
body{
  background-image: url('fondo.svg');
  background-size: cover;
  background-repeat: no-repeat;
  background-color: #333;
  overflow: hidden;
}
.App{
  display: grid;
  grid-template: 10vh 5vh 75vh 4vh 6vh/ 3vw 22vw 2vw 70vw;
}
h1{
  color:#fff;
  text-shadow: #000 3px 3px;
}
*::-webkit-scrollbar{
  width: 9px;
  box-sizing: border-box;
}
*::-webkit-scrollbar-thumb{
  background-color: #fff9;
  border-radius: 5px;
}
#miNombre{
  grid-column: 2/3;
  grid-row: 1/3;
  justify-self: center;
  align-self: center;
  font-family: Robota;
  font-size: 2.5vw;
  font-weight: 100;
}
.Cargando{
  display:grid;
  grid-template:1fr 1fr/1fr;
  justify-items: center;
  align-items: center;
  color:#fff;
}
.Cargando h3{
  font-size: 8vh;
}

@media (max-width:700px){
  .App{
    grid-template:10vh 5fr 1fr /1fr 90vw 1fr !important;
    height: 100vh;
    overflow: hidden;
  }
  .Menu{
    grid-column: 1/-1;
    grid-row: 5/-1;
    overflow-x: scroll;
    height: 20vh;
    display: grid;
  }
  .Menu> .sombra{
    grid-template: 1fr/repeat(6,25vw);
    grid-gap: 0px;
    padding-bottom: 2vh;
    align-self: flex-end;
  }
  .boton{
    display: grid;
    height: 100%;
    align-items: center;
  }
  #miNombre{
    grid-column:2/3;
    grid-row:1/2;
    font-size: 10vw;
  }
  .Visor{
    grid-column: 2/3;
    grid-row: 2/4;
  }
  #yo{
    width: 50vw;
    height: 50vw !important;
    clip-path: none !important;
    shape-outside: none !important;
    border-radius: 100%;
    position: relative;
    left:15vw;
    float:none !important;
  }
  h1{
    text-align: center;
  }
}
